



let carts = document.querySelectorAll('.buy')
let products = [
    {tag: 1, name: 'title1', price: 10, inCart:0},
    {tag: 2, name: 'title2', price: 20, inCart:0},
    {tag: 3, name: 'title3', price: 30, inCart:0}
]


for (let i=0; i<carts.length; i++){
    carts[i].addEventListener('click', () => {
        cartNumbers(products[i])
        totalCost(products[i])
    })
}

//Ð¿ÐµÑ€ÐµÐ²Ñ–Ñ€ÑÑ” local storage Ñ– Ð²Ð¸Ð²Ð¾Ð´Ð¸Ñ‚ÑŒ Ð² Ð¼ÐµÐ½ÑŽ ÐºÑ–Ð»ÑŒÐºÑ–ÑÑ‚ÑŒ
function onLoadCartNumbers(){
    let productNumbers = localStorage.getItem('cartNumber')
    if (productNumbers){
        document.querySelector('number-in-cart').textContent = productNumbers
    }
}


//Ñ€Ð°Ñ…ÑƒÑ” Ð½Ð°Ñ‚Ð¸ÑÐºÐ°Ð½Ð½Ñ Ð½Ð° Ð¾ÑÐ½ ÑÑ‚Ð¾Ñ€Ñ–Ð½Ñ†Ñ–
function cartNumbers(product){
    let productNumbers = localStorage.getItem('cartNumbers')// cartNumbers -- Ð·Ð°Ð³Ð°Ð»ÑŒÐ½Ð° ÐºÑ–Ð»ÑŒÐºÑ–ÑÑ‚ÑŒ Ñ‚Ð¾Ð²Ð°Ñ€Ñ–Ð² Ñƒ ÐºÐ¾Ñ€Ð·Ð¸Ð½Ñ–, Ð´Ð»Ñ Ð²Ð¸Ð²Ð¾Ð´Ñƒ Ð² Ð¼ÐµÐ½ÑŽ index.html

    productNumbers = parseInt(productNumbers)
    
    if (productNumbers){
        localStorage.setItem('cartNumbers', productNumbers+1)
        document.querySelector('.number-in-cart').textContent =productNumbers+1
    }else {
        localStorage.setItem('cartNumbers', 1)
        document.querySelector('.number-in-cart').textContent =1
    }
    setItems(product)
}

//Ñ€Ð°Ñ…ÑƒÑ” ÐºÑ–Ð»ÑŒÐºÑ–ÑÑ‚ÑŒ Ñ€ÐµÑ‡ÐµÐ¹ Ð´Ð»Ñ ÐºÐ¾Ð¶Ð½Ð¾Ð³Ð¾ Ð²Ð¸Ð´Ñƒ
function setItems(product){
    let cartItems=localStorage.getItem('productsInCart') //Ðº-ÑÑ‚ÑŒ ÐºÐ¾Ð¶Ð½Ð¾Ð³Ð¾ Ð²Ð¸Ð´Ñƒ
    cartItems = JSON.parse(cartItems);

    if(cartItems !=null){
        if (cartItems[product.tag]==undefined){
            cartItems={
                ...cartItems,
                [product.tag]:product
            }
        }
        cartItems[product.tag].inCart+=1
    }else{
    product.inCart = 1
    cartItems = {[products.tag]:product}
    }
    localStorage.setItem('productsInCart', JSON.stringify(cartItems))
}

function totalCost(product){
    let cartCost = localStorage.getItem('totalCost')
    
    if (cartCost != null){
        cartCost = parseInt(cartCost)
        localStorage.setItem('totalCost', cartCost + product.price)
    }else{
        localStorage.setItem("totalCost", product.price)
    }
}

function displayCart(){
    let cartItems = localStorage.getItem('productsInCart')
    cartItems = JSON.parse(cartItems)
    let productContainer = document.querySelector('.products')
    let cartCost = localStorage.getItem('totalCost')
    if (cartItems && productContainer) {
        productContainer.innerHTML='';
        Object.values(cartItems).map(item =>{
            productContainer.innerHTML += `
            <div class="products" id="${item.tag}">
                <span class="cart-name">${item.name}</span>

                <div class="price">${item.price}</div>
                <div class="quantity"> 
                <button class="increase">+</button> 
                <span class="number-of-item-incart">${item.inCart}</span>
                <button class="decrease">-</button>
                </div>
                <div class="total">
                    ${item.inCart * item.price},00
                </div>
            </div>
            `
        })
        productContainer.innerHTML += `
        <div class="basketTotalContainer">
            <h4 class="BasketTotalTitle">
                Basket Total </h4>
            <h4> 
                ${cartCost},00
            </h4>
        </div>
        `
    }
}



